.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#profilePhoto {
  max-width: 444px;
  max-height: 444px;
  float: left;
}

@media only screen and (max-width: 575px)  {
  #profilePhoto {
    max-width: 558px;
    max-height: 558px;
  }
}

#info div {
  float: left;
}

#info, .imgDiv {
  padding-left: 0px;
}

#info {
  background: #fff;
  margin-top: 200px;
}

.subTitle {
  font-size: 1.4rem;
}

body {
  background: #e7e7e7;
  font-family: 'Quicksand', sans-serif;
}

.shadow {
  -webkit-box-shadow: 0px 0px 12px -1px rgba(79,79,79,1);
  -moz-box-shadow: 0px 0px 12px -1px rgba(79,79,79,1);
  box-shadow: 0px 0px 12px -1px rgba(79,79,79,1);
}

.maxWidth {
  min-height: 375px;
  min-width: 100%;
  background-color: #355c7d;
  position: absolute;
  color: #fff;
}

.colorized {
  color: #355c7d;
}

#welcome span {
  background: #355c7d;
  color: #fff;
  padding: 5px;
}

#welcome {
  margin-top: 30px;
}

#info .col-sm-7 * {
  text-align: left;
}

.headingLinks {
  height: 50px;
  background: #355c7d;
  color: #fff;
  width: 106%;
  position: absolute;
  bottom: -52px;
  left: -24px;
}

.headingLinks .col-sm-2 {
  text-align: center !important;
}

.headingLinks .col-sm-2 svg{
  font-size: 1.8rem;
  margin-top: 10px;
}

a {
  color: #000;
}

.headingLinks a{
  color: #fff;
}

.top60 {
  margin-top: 60px;
  text-align: left;
}

.top60c {
  margin-top: 60px;
  text-align: center;
}

.left {
  float: left;
}

.right {
  float: right;
}

.skillsDiv {
  background-color: #fff;
  margin-top: 40px;
  padding: 15px;
}

.react-sweet-progress-line-inner, .react-sweet-progress-line-inner-status-active {
  background-color: rgb(53, 92, 125) !important;
}

.top15 {
  margin-top: 10px;
}

button svg {
  font-size: 1.4rem;
  margin-left: 5px;
}

#skillzResume {
  text-align: left;
}

.skillzTitle p {
  margin: 0px !important;
  margin-top: 10px !important;
}